import React, { useEffect, useRef } from 'react';
import * as THREE from 'three';
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader';
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry';

const SpinningGlobe = () => {
    const mountRef = useRef(null);
    const textMeshRef = useRef(null);

    useEffect(() => {
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
        const renderer = new THREE.WebGLRenderer();
        renderer.setSize(window.innerWidth, window.innerHeight);
        renderer.setClearColor(0xeeeeee, 1); // Light gray background
        const mount = mountRef.current;
        mount.appendChild(renderer.domElement);

        // Create the globe
        const geometry = new THREE.SphereGeometry(5, 32, 32);
        const texture = new THREE.TextureLoader().load(
            'https://upload.wikimedia.org/wikipedia/commons/0/02/Earth_Western_Hemisphere.jpg', 
            (texture) => {
                // This callback is executed when the texture is loaded successfully
                globe.material.map = texture;
                globe.material.needsUpdate = true; // Notify Three.js that the material needs to be updated
            },
            undefined,
            (error) => {
                console.error('Error loading texture:', error);
            }
        );
        const material = new THREE.MeshBasicMaterial({ map: texture });
        const globe = new THREE.Mesh(geometry, material);
        scene.add(globe);

        // Load the font and create text
        const loader = new FontLoader();
        loader.load('https://threejs.org/examples/fonts/helvetiker_regular.typeface.json', (font) => {
            const textGeometry = new TextGeometry('Your Skills', {
                font: font,
                size: 1,
                depth: 0.1,
            });

            const textMaterial = new THREE.MeshBasicMaterial({ color: 0xffffff });
            const textMesh = new THREE.Mesh(textGeometry, textMaterial);
            textMeshRef.current = textMesh;
            scene.add(textMesh);

            // Position and rotate the text
            textMesh.position.set(0, 0, 6);
            textMesh.rotation.x = 0; // Set this to 0 to correct text orientation
            textMesh.rotation.y = 0; // Set this to 0 to correct text orientation
        }, undefined, (error) => {
            console.error('An error occurred loading the font:', error);
        });

        camera.position.set(0, 0, 15); // Move camera further away

        const animate = () => {
            requestAnimationFrame(animate);
            globe.rotation.y += 0.01;
            if (textMeshRef.current) {
                textMeshRef.current.lookAt(camera.position);
            }
            renderer.render(scene, camera);
        };

        animate();

        return () => {
            mount.removeChild(renderer.domElement);
        };
    }, []);

    return <div ref={mountRef} />;
};

export default SpinningGlobe;
